
.orderSuccess {
    margin: auto;
    text-align: center;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    display: flex;
    height: 50vh;
    padding: 10vmax;
    transition: all 0.5s;
    
  
  }
  .orderSuccess > svg {
    font-size: 5vmax;
    color: tomato;
    animation-name:creating;
    animation-duration: 0.5s;
    transform-origin: center;
    transition: all 0.5s;
   
  }
  @keyframes creating {
    0% {
      transform: scale(0);
    }
    100% {
      transform: scale(1);
    }
  }
  .orderSuccess > p {
    font: 300 2vmax "Roboto";
    margin: 1vmax;
  }
  
  
  .orderSuccess > a {
    background-color: rgb(51, 51, 51);
    color: #fff;
    font: 400 1vmax "Roboto";
    text-decoration: none;
    padding: 1vmax 3vmax;
    border: none;
    cursor: pointer;
    transition: all 0.1s;
  }
  .orderSuccess > a:hover {
    background-color: rgb(5, 5, 5);
  }

  @media screen and (max-width:600px) {
    .orderSuccess {
 
        height: 70vh;
       
      
      }
      .orderSuccess > svg {
        font-size: 10vmax;
      }
      
      .orderSuccess > p {
        font: 300 3vmax "Roboto";
        margin: 2vmax;
      }
      
      
      .orderSuccess > a {
       
        font: 400 2.5vmax "Roboto";
        
      }
  }